.Done {
  max-width: 500px;
  width: 90%;
  margin: auto;

  h2 {
    margin: 10px 0;
    font-size: 20px;
    color: #fff;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .addBar {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;

    button {
      margin-left: 10px;
    }
  }

  .loading {
    width: 20px;
    height: 20px;
    margin-left: 10px;
  }
}
